	function generateShapes() {
		let x;
		for (x =0; x<5; x++){

		if (x%2 == 0){
			Shapelayer.add(
			new Konva.Rect({
			  x: Math.random() * stage.width(),
			  y: Math.random() * stage.height(),
			  width: 50 + Math.random() * 50,
			  height: 50 + Math.random() * 50,
			  fill: Konva.Util.getRandomColor(),
			  rotation: Math.random() * 360,
			  draggable: true,
			  name: 'object'
			})
		  );}
		  else {
			Shapelayer.add(new Konva.Circle({
			x: Math.random() * stage.width(),
			y: Math.random() * stage.height(),
			radius: 70,
			  width: 50 + Math.random() * 50,
			  height: 50 + Math.random() * 50,
			fill: Konva.Util.getRandomColor(),
			stroke: Konva.Util.getRandomColor(),
			strokeWidth: 1,
			  rotation: Math.random() * 360,
			  draggable: true,
			  name: 'object'
			}));
		  }
			stage.add(Shapelayer);
			Shapelayer.draw();
		}
	}

	function clearCanvas() {
		//This basically destorys the layer.
		stage.destroyChildren();
		console.log(stage.getLayers());
		stage = new Konva.Stage({
			container: 'konva-stage',   // id of container <div>
			width: 725,
			height: 350
		});
		layer = new Konva.Layer();
		bglayer = new Konva.Layer();
		stage.add(bglayer)
		bglayer.moveToBottom()
		console.log("Canvas was cleared");	
	}

	function deleteLastElement() {
		//This basically destorys the layer, so we will use it to delete last element.
		layer.destroy();
		layer = new Konva.Layer();
	}

	function downloadURI(uri, name) {
        let link = document.createElement('a');
        link.download = name;
        link.href = uri;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }

	function downloadIMG() {
		//remove transformer from the rect or selected shapes
		stage.find('Transformer').destroy();
		// reseting the canvas size incase user is using on mobile and downloads image for twitter which should be enlarged na of right dimension.
		//stage.width = 780;
		//stage.height = 350;

		let isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
		if (isMobile) {
			  console.log("You on Mobile")
			  if (document.querySelector('#fb:checked') !== null)
					{
					var dataURL = stage.toDataURL({ 
						pixelRatio: 4 });
					downloadURI(dataURL, 'posterFB.png');
					}

					if (document.querySelector('#ln:checked') !== null)
					{
					var dataURL = stage.toDataURL({ 
						pixelRatio: 4 });
					downloadURI(dataURL, 'posterLN.png');
					}

					if (document.querySelector('#tw:checked') !== null)
					{
					var dataURL = stage.toDataURL({  pixelRatio: 2 });
					downloadURI(dataURL, 'posterTW.png');
					}
		} else {
  			console.log("You on Desktop")
					if (document.querySelector('#fb:checked') !== null)
					{
					var dataURL = stage.toDataURL({ 
						pixelRatio: 2 });
					downloadURI(dataURL, 'posterFB.png');
					}

					if (document.querySelector('#ln:checked') !== null)
					{
					var dataURL = stage.toDataURL({ 
						pixelRatio: 2 });
					downloadURI(dataURL, 'posterLN.png');
					}

					if (document.querySelector('#tw:checked') !== null)
