	}
	afterDownload();
	}

	function openUploadPopUp(){
		document.getElementById('userImgUpload').click()
	}

	function addIMGtoStage(event){
		var image = new Image();
		image.src = URL.createObjectURL(event.target.files[0]);

		image.onload = function () {
			let newIMG = new Konva.Image({
			x: 50,
			y: 50,
			image: image,
			width: 106,
			height: 118,
			draggable:true,
			name: 'object'
			});
			layer = new Konva.Layer();
			layer.add(newIMG);
			layer.batchDraw();
			stage.add(layer)
		}
	}

	beforeUpdate(() => {
		console.log("beforeUpdate was called");
		//setting default values to canvas dimensions
		width = 725;
		height = 350;
		// then create default one layer
		layer = new Konva.Layer();
	});

	afterUpdate(() =>{
		console.log("AfterUpdate was called");
		function fitStageIntoParentContainer() {
        let lcontainer = document.getElementById('stage-parent');

        // now we need to fit stage into parent
        var containerWidth = document.getElementById('stage-parent').offsetWidth;
        // to do this we need to scale the stage
        var scale = containerWidth / width;

        stage.width(width * scale);
        stage.height(height * scale);
        stage.scale({ x: scale, y: scale });
        stage.draw();
      }

      fitStageIntoParentContainer();
      // adapt the stage on any window resize
      window.addEventListener('resize', fitStageIntoParentContainer);
	});

	onMount(() => {
		console.log("OnMount was called");
		console.log("Checking for Pro Access");
		proAccessCheck();
		
		// first we need to create a stage
		stage = new Konva.Stage({
			container: 'konva-stage',   // id of container <div>
			width: width,
			height: height
		});
		bglayer = new Konva.Layer();
		stage.add(bglayer)
		bglayer.moveToBottom()

		let tooltipLayer = new Konva.Layer();
		let tooltip = new Konva.Label({
			opacity: 0.75,
			visible: false,
			listening: false
		  });
		tooltip.add(
			new Konva.Tag({
			  fill: 'black',
			  pointerDirection: 'down',
			  pointerWidth: 10,
			  pointerHeight: 10,
			  lineJoin: 'round',
			  shadowColor: 'black',
			  shadowBlur: 10,
			  shadowOffset: 10,
			  shadowOpacity: 0.2
			})
		  );
		tooltip.add(
		  new Konva.Text({
			text: 'Test',
			fontFamily: 'Calibri',
			fontSize: 18,
			padding: 5,
			fill: 'white'
		  })
		);
		
		tooltipLayer.add(tooltip);

		//generateShapes()
		// were can we snap our objects?
		function getLineGuideStops(skipShape) {
			// we can snap to stage borders and the center of the stage
			let vertical = [0, stage.width() / 2, stage.width()];
			let horizontal = [0, stage.height() / 2, stage.height()];
			// and we snap over edges and center of each object on the canvas
			stage.find('.object').forEach(guideItem => {
			  if (guideItem === skipShape) {
				return;
			  }
			  let box = guideItem.getClientRect();
			  // and we can snap to all edges of shapes
			  vertical.push([box.x, box.x + box.width, box.x + box.width / 2]);
			  horizontal.push([box.y, box.y + box.height, box.y + box.height / 2]);
			});
			return {
			  vertical: vertical.flat(),
			  horizontal: horizontal.flat()
			};
		  }
		  // what points of the object will trigger to snapping?
		  // it can be just center of the object
		  // but we will enable all edges and center
		function getObjectSnappingEdges(node) {
			let box = node.getClientRect();
			return {
			  vertical: [
				{
				  guide: Math.round(box.x),
				  offset: Math.round(node.x() - box.x),
				  snap: 'start'
				},
				{
				  guide: Math.round(box.x + box.width / 2),
				  offset: Math.round(node.x() - box.x - box.width / 2),
				  snap: 'center'
				},
				{
				  guide: Math.round(box.x + box.width),
				  offset: Math.round(node.x() - box.x - box.width),
				  snap: 'end'
				}
			  ],
			  horizontal: [
				{
				  guide: Math.round(box.y),
				  offset: Math.round(node.y() - box.y),
				  snap: 'start'
				},
				{
				  guide: Math.round(box.y + box.height / 2),
				  offset: Math.round(node.y() - box.y - box.height / 2),
				  snap: 'center'
				},
				{
				  guide: Math.round(box.y + box.height),
				  offset: Math.round(node.y() - box.y - box.height),
				  snap: 'end'
				}
			  ]
			};
		  }
		  // find all snapping possibilities
		function getGuides(lineGuideStops, itemBounds) {
			let resultV = [];
			let resultH = [];
			lineGuideStops.vertical.forEach(lineGuide => {
			  itemBounds.vertical.forEach(itemBound => {
				let diff = Math.abs(lineGuide - itemBound.guide);
				// if the distance between guild line and object snap point is close we can consider this for snapping
				if (diff < GUIDELINE_OFFSET) {
				  resultV.push({
					lineGuide: lineGuide,
					diff: diff,
					snap: itemBound.snap,
					offset: itemBound.offset
				  });
				}
			  });
			});
			lineGuideStops.horizontal.forEach(lineGuide => {
			  itemBounds.horizontal.forEach(itemBound => {
				let diff = Math.abs(lineGuide - itemBound.guide);
				if (diff < GUIDELINE_OFFSET) {
				  resultH.push({
					lineGuide: lineGuide,
					diff: diff,
					snap: itemBound.snap,
					offset: itemBound.offset
				  });
				}
			  });
			});
		  let guides = [];
			// find closest snap
		  let minV = resultV.sort((a, b) => a.diff - b.diff)[0];
		  let minH = resultH.sort((a, b) => a.diff - b.diff)[0];
		  if (minV) {
			  guides.push({
				lineGuide: minV.lineGuide,
				offset: minV.offset,
				orientation: 'V',
				snap: minV.snap
			  });
			}
		  if (minH) {
			  guides.push({
				lineGuide: minH.lineGuide,
				offset: minH.offset,
				orientation: 'H',
				snap: minH.snap
			  });
			}
		  return guides;
		}
		function drawGuides(guides) {
			guides.forEach(lg => {
			  if (lg.orientation === 'H') {
				let line = new Konva.Line({
				  points: [-6000, lg.lineGuide, 6000, lg.lineGuide],
				  stroke: 'rgb(0, 161, 255)',
				  strokeWidth: 1,
				  name: 'guid-line',
				  dash: [4, 6]
				});
				layer.add(line);
				layer.batchDraw();
			  } else if (lg.orientation === 'V') {
				let line = new Konva.Line({
				  points: [lg.lineGuide, -6000, lg.lineGuide, 6000],
				  stroke: 'rgb(0, 161, 255)',
				  strokeWidth: 1,
				  name: 'guid-line',
				  dash: [4, 6]
				});
				layer.add(line);
				layer.batchDraw();
			  }
			});
		  }
		layer.on('dragmove', function(e) {
			// clear all previous lines on the screen
			layer.find('.guid-line').destroy();
			// find possible snapping lines
			let lineGuideStops = getLineGuideStops(e.target);
			// find snapping points of current object
			let itemBounds = getObjectSnappingEdges(e.target);
			// now find where can we snap current object
			let guides = getGuides(lineGuideStops, itemBounds);
			// do nothing of no snapping
			if (!guides.length) {
			  return;
			}
		drawGuides(guides);
		// now force object position
		guides.forEach(lg => {
			  switch (lg.snap) {
				case 'start': {
				  switch (lg.orientation) {
					case 'V': {
					  e.target.x(lg.lineGuide + lg.offset);
					  break;
					}
					case 'H': {
					  e.target.y(lg.lineGuide + lg.offset);
					  break;
					}
				  }
				  break;
				}
				case 'center': {
				  switch (lg.orientation) {
					case 'V': {
					  e.target.x(lg.lineGuide + lg.offset);
					  break;
					}
					case 'H': {
					  e.target.y(lg.lineGuide + lg.offset);
					  break;
					}
				  }
				  break;
				}
				case 'end': {
				  switch (lg.orientation) {
					case 'V': {
					  e.target.x(lg.lineGuide + lg.offset);
					  break;
					}
					case 'H': {
					  e.target.y(lg.lineGuide + lg.offset);
					  break;
					}
				  }
				  break;
				}
			  }
			});
		  });
		layer.on('dragend', function(e) {
		  // clear all previous lines on the screen
		  layer.find('.guid-line').destroy();
		  layer.batchDraw();
		});
		layer.draw();
		
		stage.on('click tap', function(e) {
		  // if click on empty area - remove all transformers
		  if (e.target === stage) {
			stage.find('Transformer').destroy();
			layer.draw();
			return;
		  }
		  // do nothing if clicked NOT on our rectangles
		  if (!e.target.hasName('object')) {
			return;
		  }
		  // remove old transformers
		  // TODO: we can skip it if current rect is already selected
		  stage.find('Transformer').destroy();
		  // create new transformer
		  let tr = new Konva.Transformer();
		  layer.add(tr);
		  tr.attachTo(e.target);
		  layer.draw();
		  let mousePos = e.getStage().getPointerPosition();
		  tooltip.position({
			  x: mousePos.x,
			  y: mousePos.y - 5
		  });
		  tooltip
			  .getText()
			  .text('object: ' + e.id() + ', color: ' + e.fill());
			tooltip.show();
			tooltipLayer.batchDraw();
		  });
		
		stage.on('mouseout', function(evt) {
		  tooltip.hide();
		  tooltipLayer.draw();
		});

		// add the layer to the stage
		stage.add(tooltipLayer);
		stage.add(layer);
		// draw the image
		layer.draw();
		
		});

	</script>
	<div>
		<!--<div class="position-fixed" style="z-index: 9999999">
			<span class="badge badge-warning d-inline-block d-sm-none">XS</span>
			<span class="badge badge-warning d-none d-sm-inline-block d-md-none">SM</span>
			<span class="badge badge-warning d-none d-md-inline-block d-lg-none">MD</span>
			<span class="badge badge-warning d-none d-lg-inline-block d-xl-none">LG</span>
			<span class="badge badge-warning d-none d-xl-inline-block">XL</span>
		</div>	-->	

	<Row class="mt-3">
		<!-- Canvas / Stage / Layer Component-->
		<Col lg="6">
			<div id="stage-parent">
					<div id="konva-stage"></div>
			</div>
			<Row class="mt-2">
				<Col>
				<Button block color="success" size="lg" on:click={toggleLg}>Select and Download</Button>
				<Modal isOpen={open} {toggle} {size}>
					<ModalHeader {toggle}>Select Your Channels</ModalHeader>
					<ModalBody>
					  <Row>
						  <Col>
							<CustomInput
        type="checkbox"
        name="fb" id="fb" label="Facebook"  />
						  </Col>
						  <Col>
							<CustomInput
        type="checkbox"
        name="tw" id="tw" label="Twitter" />
						  </Col>
					  </Row>
					  <Row>
						  <Col>
							<CustomInput
        type="checkbox"
        name="ln" id="ln" label="LinkedIn" />
						  </Col>
						  <Col>
							<CustomInput
        type="checkbox"
        name="ig" id="ig" label="Instagram (Soon)" disabled />
						  </Col>
					  </Row>
					  <Row>
						  <Col>
						  <div class="centerInfoText">
							{#if toRemix}
							<span>Content WILL be remixed and unique. You can change the setting in "Remix" Tab.</span>
						{:else}
						<span>Content WILL NOT be remixed and unique. You can change the setting in "Remix" Tab.</span>
						{/if}
						  </div>
						</Col>
					  </Row>
					</ModalBody>
					<ModalFooter>
					  <Button color="primary" on:click={selectAndDownload}>
						Download My Files
					  </Button>
					  <Button color="secondary" on:click={toggle}>
						Cancel
					  </Button>
					</ModalFooter>
				  </Modal>
				</Col>
			</Row>
		</Col>

	<Col lg="5" offset="1">
<Row>
<Col>
	<Tabs>
		<TabList>
